* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-decoration: none;
  text-transform: capitalize;
  transition: all 0.3s linear;
  outline: none;
}
header {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),
    url("./images/background.jpg");
  background-position: center;
  background-size: cover;
  clip-path: polygon(100% 0, 100% 91%, 49% 100%, 0 89%, 0 0);
}
nav {
  padding: 2% 6%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}
nav .logo {
  font-size: 3rem;
  cursor: pointer;
  color: #ffa31a;
}
nav .links {
  gap: 1rem;
  padding-left: 0;
}
nav .links li {
  display: inline-block;
  margin-left: 1rem;
  font-size: 1.3rem;
}
.navlink {
  color: white;
  cursor: pointer;
}
nav .links li .navlink:hover {
  color: #ffa31a;
}
header .content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
header .content h1 {
  font-size: 6rem;
  text-transform: uppercase;
  color: white;
}
.btn {
  padding: 0.5rem 1.5rem;
  background-color: transparent;
  border: 0.3rem solid #ffa31a;
  color: #ffa31a;
  margin-top: 1.8rem;
  cursor: pointer;
  font-size: 1.5rem;
  border-radius: 0.5rem;
}
.btn:hover {
  background-color: #ffa31a;
  color: rgb(0, 0, 0);
}
nav .open,
.close {
  font-size: 1.8rem;
  cursor: pointer;
  color: #ffa31a;
  display: none;
}
@media (max-width: 850px) {
  nav .links {
    height: 25rem;
    width: 60%;
    position: fixed;
    top: 0;
    right: 0;
    border-radius: 1.2rem;
    background-color: white;
    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 9999;
  }
  nav .links li {
    font-size: 1.8rem;
    margin: 0.5rem;
  }
  nav .links li navlink {
    color: black;
  }
  nav .open,
  nav .links .close {
    display: block;
    position: absolute;
    top: 1.8rem;
    right: 1.8rem;
  }
  header .content h1 {
    font-size: 6rem;
  }
}
.about .row {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 2% 6%;
}
.about .row .image {
  flex: 1 1 40rem;
  padding: 2rem;
}
.about .row .image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about .row .content {
  flex: 1 1 40rem;
  padding: 2rem;
}
.about .row .content h1 {
  font-size: 3rem;
  color: #ffa31a;
  margin-bottom: 5rem;
}
.about .row .content p {
  line-height: 1.7;
  font-size: 1.2rem;
  margin-bottom: 1.2rem;
}
.services {
  min-height: 100vh;
  text-align: center;
  padding: 2% 6%;
}
.services h1 {
  font-size: 3.8rem;
  padding: 0.5rem;
  display: inline-block;
  color: #ffa31a;
  border-bottom: 0.3rem solid #ffa31a;
  margin-bottom: 1.8rem;
}
.services .container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
}
.services .container .box {
  width: 21rem;
  height: 21rem;
  margin: 0.5rem;
  position: relative;
  overflow: hidden;
}
.services .container .box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.services .container .box .info {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transform-origin: bottom;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.services .container .box .info h3 {
  font-size: 2rem;
  color: #ffa31a;
  margin-bottom: 1.2rem;
}
.services .container .box:hover .info {
  transform: translate(-50%, -50%) scale(1);
}
.services .container .box .info p {
  line-height: 2;
  font-size: 1rem;
  color: #ffffff;
  margin-bottom: 1.2rem;
  padding: 0 0.8rem;
  margin-bottom: 1.2rem;
  background-color: #000;
}
.pricing_table {
  min-height: 100vh;
  padding: 2% 6%;
  text-align: center;
}
.pricing_table h1 {
  font-size: 3.8rem;
  padding: 0.5rem;
  display: inline-block;
  color: #ffa31a;
  border-bottom: 0.3rem solid #ffa31a;
  margin-bottom: 2.2rem;
}
.pricing_table .pricing_card {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
}
.pricing_card .pricing_box {
  width: 25rem;
  box-shadow: 2px 2px 6px #000;
  margin-bottom: 4rem;
}
.pricing_card .pricing_box:hover {
  transform: translateY(-30px);
}
.pricing_box h2 {
  font-size: 2rem;
  margin-bottom: 3rem;
  background-color: #ffa31a;

  padding: 1.2rem;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
}
.pricing_box h3 {
  margin-top: 2rem;
  font-size: 4rem;
  color: #ffa31a;
}
.pricing_box h3 sup {
  font-size: 2rem;
}
.pricing_box h3 span {
  font-size: 2.5rem;
}
.pricing_box p {
  font-size: 2.5rem;
  color: #333;
}
.pricing_box ul {
  padding: 2rem;
}
.pricing_box ul li {
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
}
.pricing_box .btn {
  margin-bottom: 2rem;
}
.register {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url("./images/register.jpg");
  min-height: 100vh;
  width: 100%;
  background-position: center;
  background-size: cover;
  text-align: center;
}
.register h1 {
  font-size: 3.8rem;
  padding: 0.5rem;
  display: inline-block;
  color: #ffa31a;
  border-bottom: 0.3rem solid #ffa31a;
  margin-bottom: 2.2rem;
}
.register form {
  padding: 2% 6%;
  margin: 0 auto;
}
.register form .input_box {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.register form .input_box input,
textarea {
  height: 2rem;
  width: 49%;
  background-color: transparent;
  color: #ddd;
  padding: 2.5rem 0.5rem;
  margin: 1rem 0;
  font-size: 2rem;
  border: 1px solid #ddd;
}
.register form textarea {
  height: 20rem;
  width: 100%;
  resize: none;
  padding: 1rem;
}
@media (max-width: 1100px) {
  .register form .input_box input {
    width: 100%;
  }
}
